<template>
    <div class="address-option">
      <form @submit.prevent="submitCrad" id="form">
        <div class="info-list">
          <div class="list">
            <div class="tit">银行卡号<span></span></div>
            <div class="list-content">
              <input type="number" name="bank_card" v-model="cardInfo.bank_card" placeholder="请输入卡号">
            </div>
          </div>
          <div class="list">
            <div class="tit">持卡人<span></span></div>
            <div class="list-content">
              <input type="text"  name="real_name" v-model="cardInfo.real_name" placeholder="请输入持卡人姓名">
            </div>
          </div>
        </div>
        <div class="bottom-btn">
          <van-button  v-if="isEdit" class="btn-login cancel-btn" type="default" @click.prevent="deleteItem" plain>删除</van-button>
          <van-button  class="btn-login" type="default" native-type="submit" plain>确认</van-button>
        </div>
      </form>
    </div>
</template>

<script type="text/ecmascript-6">
import {  Dialog, Toast  } from 'vant';
import { apiPost, apiGet } from "../../common/js/api.js"
export default {
    name: "address-option",
    data(){
      const query = this.$route.query
      return{
        query:query,
        creditCardId:query.id,
        isEdit:query.id?true:false,//true表示编辑地址
        cardInfo:{},
      }
    },
    components: { },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      if(this.isEdit){
        this.cardInfo.bank_card = this.query.bank_card
        this.cardInfo.real_name = this.query.real_name
        this.cardInfo.id = this.query.id
      }
      
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //删除银行卡
      deleteItem(){
        Dialog.confirm({
          title: '警告',
          message: '是否确认删除该银行卡？'
        }).then(() => {
          // on confirm
          let formaData = new FormData(form)
          formaData.append("accessToken",this.$cookie.get('accessToken'))
          formaData.append("bank_id",this.query.id)
          
          apiPost('/bank/bank-del',formaData).then(res=>{
            this.$router.go(-1)
          })
        }).catch(() => {
          // on cancel
        });
      },
      //保存地址
      submitCrad(){
        const { real_name, bank_card } = this.cardInfo
        if(!bank_card){
          Toast("请输入银行卡号")
          return
        }
        if(!real_name){
          Toast("请输入真实姓名")
          return
        }
        
        let form = document.getElementById("form")
        let formaData = new FormData(form)
        formaData.append("accessToken",this.$cookie.get('accessToken'))
        
        let api = ""
        if(!this.isEdit){
          api="/bank/add"
        }else{
          api="/bank/edit"
          formaData.append("id",this.query.id)
        }
        apiPost(api,formaData).then(res=>{
          this.$router.go(-1)
        })
      }
    }
}
</script>

<style scoped lang="scss">
  @import "./../../common/css/common.css";
  .address-option{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding:.18rem .13rem;
  }
  .info-list{
    background: #fff;
    border-radius: .04rem;
    width: 100%;
    padding:.05rem .04rem;
    margin:0 0 .73rem 0;
    .list{
      height:.5rem;
      font-size: .17rem;
      color: #9A9A9A;
      border-bottom: .01rem solid #F5F5F5;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding:0 .09rem;
      &:nth-last-child(1){
        border:0;
      }
      &:nth-last-child(2){
        border:0;
      }
      .tit{
        width: .68rem;
        height: 100%;
        line-height: .5rem;
        margin-right: .35rem;
        color: #333333;
        text-align: justify;
        &>span { 
          display: inline-block /* Opera */;
          padding-left: 100%; 
          // width:100%;
        }
      }
      .list-content{
        flex: 1;
        display: flex;
        justify-content: flex-end;
        height: 100%;
        align-items: center;
        color: #9A9A9A;
        .head{
          width: .31rem;
          height: .31rem;
          border-radius: 50%;
        }
        .icon-yousanjiao1{
          margin-left: .13rem;
        }
        &>input{
          text-align: left;
          border:0;
          height: 100%;
        }
      }
    }
  }
  .bottom-btn{
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    height: .47rem;
    width:100%;
  }
  .btn-login{
    width: 50%;
    height: .47rem;
    flex: 1;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0;
    font-size: 0.17rem;
    text-align: center;
    line-height: .47rem;
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    color: white;
  }
  .cancel-btn{
    background: #fff;
    flex: 1;
    color: #3C7FEE;
  }
</style>



